.userCard {
  background-color: var(--bg-white);
  padding: 20px;
  border-radius: 10px; 
  display: flex;
  align-items: center;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.87);
  font-weight: 400;
  font-size: 16px;
  line-height: calc(26/16);
 &:not(:last-child) {
    margin-bottom: 20px;
 }
 &:last-child{
    margin-bottom: 50px;
 }
}
.userImg {
  border-radius: 50%;
  margin-bottom: 20px;
  height: 70px;
  width: 70px;
}
.userName {
  margin-bottom: 20px;
}
